<template>
  <div class="test">
    <h1>게시판 상세보기</h1>

    <div class="AddWrap">

        <table class="tbAdd">
          <colgroup>
            <col width="15%" />
            <col width="*" />
          </colgroup>
          <tr>
            <th>제목</th>
            <td>{{title}}</td>
          </tr>
          <tr>
            <th>첨부파일</th>
            <div v-for="(file,idx) in files">
                <a href="javascript:;" @click="fileDownLoad(`${file.fno}`)">{{file.org_file_name}}</a>
            </div>
          </tr>
          <tr>
            <th>내용</th>
            <td class="txt_cont" v-html="content"></td>
          </tr>
        </table>

    </div>

    <div class="btnWrap">
      <a href="javascript:;" @click="fnList" class="btn">목록</a>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      body: this.$route.params,
      title: '',
      content: '',
      num: this.$route.params.seq,
      test:'',
      files:''
    }
  },

  mounted() {
    this.fnGetView();
  },

  methods:{
    fnGetView() {
      var vm = this
      const bno = {"bno":this.num}

      axios.post('/api/board/detail',bno)
        .then(res=>{

          var view = res.data[0];
          vm.title = view.title;
          vm.content = view.content.replace(/(\n)/g,'<br/>');

          axios.post('/api/board/detail/file',bno)
          .then(res=>{
            vm.files = res.data
          })

        })
        .catch(err=>{
          console.log(err);
        })
    },

    fnList(){
      delete this.body.num;
      this.$router.push({
        name:'list',query:this.body
      });
    },

    fileDownLoad(seq){

      const fno = {"fno":seq}

      axios.post("/api/fileDown",fno,{
        responseType:'blob',
      })
      .then(res=>{

        function replaceAll (str, searchStr, replaceStr) {
          return str.split(searchStr).join(replaceStr)
        }


        const url = window.URL.createObjectURL(new Blob([res.data], { type: 'image/png' }))

        console.log("url : "+url)
        console.log("res.data : "+res.data)

        const link = document.createElement('a')
        link.href = url

        const filename = replaceAll(decodeURI(res.headers.fileName), '+', ' ')

        console.log("res.headers.filename : "+decodeURI(res.headers.fileName))
        console.log("filename : "+filename)

        link.setAttribute('download', filename)

        document.body.appendChild(link)

        link.click()
        })
        .catch((error) => {
          console.log(error);
        })

    }
  }
}
</script>

<style scoped>


.AddWrap{ display: flex; position: relative; width: 1200px; margin:0 auto; }
.tbAdd{ position:relative; width: 500px; margin:0 auto;}



.tbAdd{border-top:1px solid #888;}
.tbAdd th, .tbAdd td{border-bottom:1px solid #eee; padding:5px 0;  }
.tbAdd td{padding:10px 10px; box-sizing:border-box; text-align:center;}
.tbAdd td.txt_cont{height:300px; vertical-align:top;}
.btnWrap{text-align:center; margin:20px 0 0 0;}
.btnWrap a{margin:0 10px;}
.btn {color: #333333}

</style>
