<template>
  <header>
    <h1>
      <router-link to="/"><h5>main logo</h5></router-link>
    </h1>
    <div class="menuWrap">
      <ul class="menu">

        <li><router-link to="/productList">상품게시판</router-link></li>
        <li><router-link to="/mainWrite">전체보기</router-link></li>
        <li><router-link to="/list">자유게시판</router-link></li>
        <li class="profile"><a>프로필</a>
          <ul>
            <div v-if="session">
            <li><router-link to="/login">로그인</router-link></li>
            <li><router-link to="/Register">회원가입</router-link></li>
            </div>
            <div v-else>
              {{user.name}}님 반갑습니다.
              <a href="#" @click="updateProfile">수정</a>&nbsp;
              <a href="#" @click="logout">로그아웃</a>
            </div>
          </ul>
        </li>
      </ul>
    </div>
  </header>

</template>

<script>

import axios from "axios";

export default {
  data(){
    return{
      session:true,
      id:'',
      user:''
    }
  },
  mounted() {

    let seq = localStorage.getItem("seq");

    if(seq != null){
      this.session=false
      this.id = seq
    }

    const data = {
      "mno":seq
    }

    axios.post('/api/member/data',data)
    .then(res=>{
      console.log(res.data)
      this.user = res.data[0]
    })
    .catch(err=>{
      console.log(err)
    })

    $('.profile').mouseenter(()=>{
      $('.profile > ul').stop().slideDown(500)
    })
    $('.profile').mouseleave(()=>{
      $('.profile > ul').stop().slideUp(500)
    })
  },
  methods:{

    logout(){
      this.session=!this.session
      localStorage.removeItem(this.id); // 키에 해당되는 데이터 삭제
      localStorage.clear();
      this.$router.push({
        name:"main"
      })
    },

    updateProfile(){
      this.$router.push({
        name:'profile',
        params: {'seq':this.id}
      })
    }

  }
}
</script>

<style scoped>
header{width:100%; text-align:center; position:relative; height:100px; border-bottom:1px solid #35495e}
header h1{position:absolute; top:0; left:100px;}
header ul.menu:after{display:block; clear:both; content:'';}
header ul.menu{position:absolute; top:20px; right:50px;}
header ul.menu li{float:left; padding:10px 20px; list-style:none;}

a{text-decoration:none; color:#333;}

.image{
  border: 1px solid red;
  width: auto;
}


</style>
