<template>
  <div>
  <Image></Image>
  <div v-if="session">

    <h2>게시판 리스트 </h2>

    <div class="listWrap">
      <paginated-list :list-array="PageArray"></paginated-list>

      <div class="btnRightWrap">
        <a @click="movePage" class="btn">등록</a>
      </div>
    </div>
  </div>

  <div v-else>
    <h1>login 후 이용 부탁드립니다.</h1>
  </div>
  </div>
</template>

<script>
import axios from "axios";
import PaginatedList from "@/components/page/Pagination"
import Image from '@/components/common/image'

export default {
  components:{
    PaginatedList,
    Image

  },
  data() { //변수생성
    return{
      session:false,
      PageArray:[]
    }
  }
  ,mounted() {

    let seq = localStorage.getItem("seq");
    if(seq!=null){
      this.session=true
    }

    this.getList()
  }
  , methods:{

      getList(){
        console.log("getList in")
        axios.post('/api/board/list')
        .then(res=>{
          console.log("success : "+res.data)
          this.PageArray = res.data
        })
        .catch(err=>{
          console.log("error : "+err)
        })
      },

    movePage(){
        this.$router.push({
          name:'write'
        });
    }
  }
}
</script>

