<template>
  <div class="main-container">
    <h2 class="header">회원 가입</h2>
    <hr>

    <div class="title">
      <span>이름 :</span> <input type="text" ref="name" placeholder="성함을 입력해주세요" v-model="name">
    </div>
    <hr>
    <div class="phone">
      <span>휴대번호 :</span> <input type="text" ref="phone" placeholder="번호을 입력해주세요" v-model="phone">
    </div>
    <hr>
    <div>
      <span> 카테고리 :</span>&nbsp
      <select v-model="categori" ref="categori">
        <option disabled value="">희망하는 카테고리를 선택해주세요</option>
        <option value="ee">전자기기</option>
        <option value="baby">육아용품</option>
        <option value="car">자동차용품</option>
      </select>
    </div>
    <hr>
    <div>
      <span> 물품가격 :</span>&nbsp
      <select v-model="price" ref="price">
        <option disabled value="">희망하는 물품 가격을 선택해주세요</option>
        <option v-for="opt in opt_price" :value="opt.value">
          {{opt.text}}
        </option>
      </select>
    </div>
    <hr>
    <div>
      <span> 거래위치 :</span>&nbsp
      <select v-model="place" ref="place">
        <option disabled value="">희망하는 거래 위치를 선택해주세요</option>
        <option value="YTN">YTN</option>
        <option value="DQ">Digital Cube</option>
        <option value="IFC">IFC</option>
      </select>
    </div>
    <hr>

    <div class="applyBtn">
      <button type="button" @click="regiBtn">회원 가입</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data(){
    return{
      id:this.$route.params.id,
      name:'',
      phone:'',
      categori:'',
      place:'',
      price:'',

      opt_price:[
        {text:'10000 ~ 50000', value:'0'},
        {text:'50000 ~ 100000', value:'1'},
        {text:'100000 ~ ', value:'2'}
      ]

    }
  },

  methods:{

    regiBtn() {
      if(!this.name){
        alert('성함을 작성해주세요')
        this.$refs.name.focus();
      }
      if(!this.price || !this.place || !this.categori) {
        alert('양식을 채워주세요')
        this.$refs.categori.focus();
        return;
      }

      const userData ={
        "id":this.id,
        "name":this.name,
        "phone":this.phone,
        "categori":this.categori,
        "place":this.place,
        "price":this.price
      }

      axios.post('/api/google/regi',userData)
        .then(res=>{

          let seq = res.data
          if(seq != 0){
            localStorage.setItem("seq",seq);
            this.$router.push({
              name:'/'
            })
            this.$router.go()
          }else{
            alert("문제가 발생하였습니다.")
          }



        })
        .catch(err=>{
          console.log(err)
        })


    }
  }


}
</script>

