  /*border: 1px solid #333333;*/
  margin: auto;
  margin-top: 50px;
  width: 1000px;
  height: auto;
  text-align: left;
}

.applyBtn{
  text-align: right;
}

.title input{
  width: 500px;
  height: 20px;

}


span {
  font-weight: 500
}

