<template>
  <div class="container">
    <br><br>
      <div>
        <input type="text" v-model="id" placeholder="아이디"/>
      </div>
    <div class="testing" v-if="!idTest">
      유효하지 않은 아이디 입니다.
    </div>
      <div>
        <input type="text" v-model="name" placeholder="이름"/>
      </div>
      <div>
        <input type="password" v-model="password" placeholder="비밀번호"/>
      </div>
      <div>
        <input type="text" v-model="phone" placeholder="휴대번호"/>
      </div>
      <div>
        <input type="text" v-model="email" placeholder="이메일"/>
      </div>
      <div class="testing" v-if="!emailTest">
        유효하지 않은 형식 입니다.
      </div>
      <div>
        <select v-model="categori" style="width: 147px">
          <option disabled selected>카테고리</option>
          <option v-for="option in opt_categori" :value="option.value" >
            {{option.value}}
          </option>
        </select>
      </div>
      <div>
        <select v-model="place" style="width: 147px">
          <option disabled selected>위치</option>
          <option v-for="option in opt_place" :value="option.value" >
            {{option.value}}
          </option>
        </select>
      </div>
      <div>
        <select v-model="price" style="width: 147px">
          <option disabled selected>가격</option>
          <option v-for="option in opt_price" :value="option.value" >
            {{option.text}}
          </option>
        </select>
      </div>
      <br>
      <button type="button" @click="regi">회원가입</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'SignupForm',
  data() {
    return {
      email: '',
      id: '',
      name:'',
      password: '',
      passwordConfirm: '',
      price: '',
      place:'',
      categori:'',
      phone:'',

      opt_categori:[
        {text:'YTN', value:'YTN'},
        {text:'IFC', value:'IFC'},
        {text:'DisitalCube', value:'DisitalCube'}
      ],
      opt_place:[
        {text:'123', value:'123'},

      ],
      opt_price:[
        {text:'10000 ~ 50000', value:'0'},
        {text:'50000 ~ 100000', value:'1'},
        {text:'100000 ~ ', value:'2'}
      ]

    };
  },
  computed: {
    idTest () {
      return /^[A-Za-z0-9]+$/.test(this.id)
    },
    emailTest(){
      return /^[@]+$/.test(this.email)
    }
  }
 ,
  methods: {
    regi() {

      const regi_data = {
        'id': this.id,
        'name' : this.name,
        'password' : this.password,
        'email' : this.email,
        'phone':this.phone,
        'categori':this.categori,
        'price': this.price,
        'place':this.place,
      }

      axios.post('/api/regi',regi_data)
      .then(res =>{
        console.log(res.data)
        alert("회원가입 완료")
        this.$router.push("/")
      })
      .catch(error =>{
        console.log("error : "+error)
      })


    },
  },
};
</script>

<style scoped>
  .testing{
    color: rgba(185, 66, 151, 0.75);
    font-size: 13px;
  }
</style>
