  methods:{

    fnGetView() {
      var vm = this
      const pno = {"pno":this.num}

      axios.post('/api/product/detail',pno)
        .then(res=>{
          vm.data = res.data[0];

        })
        .catch(err=>{
          console.log(err);
        })
    },

    getImg(cnt){
      var vm = this
      const pno = {"pno":this.num}

      axios.post('/api/product/detail/file',pno)
      .then(res=>{

        for(var i = 0; i< res.data.length; i++){
          vm.img[i] = require("@/assets/"+res.data[i])
        }
        this.imgSize = vm.img.length

      })
      .catch(err=>{
        console.log(err)
      })

    },

    fnList(){
      delete this.body.num;
      this.$router.push({
