<script>

import axios from "axios";

export default {
  data(){
    return{
      id:'',
      user:''
    }
  },
  mounted() {
    this.id = this.$route.params.seq

    const seq = {"mno":this.id}

    axios.post('/member/data',seq)
    .then(res =>{

      this.user = res.data[0]

    })
    .catch(err =>{
      console.log(err)
    })

  },

  methods:{


  }
}

</script>

