<template>
  <div>
   <h2>로그인 진행 중..</h2>
  </div>
</template>
<script>

  import axios from "axios";

export default {

  data(){
    return{
      link : document.location.href,
      resultCode: '',
      authCode:'',
      result:'',
      userId:''
    }
  },
  mounted() {
    this.test()
    },

  methods:{
    test() {
      var url = require('url');
      var urlObject = url.parse(this.link).query;
      var code = urlObject.split('=');

      this.result = code[1].split('&');

      console.log("auth code : "+ this.result[0]);

      const authCode = { authCode : this.result[0] }

      axios.post("/api/redirect",authCode)
        .then((res)=>{
          console.log("id: "+ res.data.id)
          console.log("status: "+ res.data.status)

          let status = res.data.status

          let id = {
            "id":res.data.id
          }


          if(status == 'true'){

            axios.post('/api/member/sequence',id)
              .then(res=>{
                localStorage.setItem("seq",res.data);
                this.$router.push('/')
                this.$router.go();
              })
              .catch(err=>{
                console.log(err)
              })


          }else{

            this.$router.push({
              name:'googleRegi',
              params:{"id":res.data.id}
            })


          }

        })
        .catch((err)=>{
          console.log("error : "+err)
        })
    }
  },

}

</script>
