import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '@/views/login/Login'
import test from '@/views/login/Redirect'
import profile from '@/views/profile/Profile'
import list from '@/views/board/list'
import chat from '@/views/chat/chat'
import regi from '@/views/login/Register'
import write from '@/views/board/write'
import view from '@/views/board/view'
import PaginatedList from '@/components/page/Pagination'
import mainPagination from '@/components/page/mainPagination'
import mainPage from '@/views/mainboard/mainPage'
import maingWrite from '@/views/mainboard/write'
import productList from '@/views/mainboard/productList'
import mainDetail from '@/views/mainboard/mainDetail'
import commentList from "@/components/comment/commentList";
import image from '@/components/common/image';
import googleRegi from '@/views/login/GoogleRegi';

Vue.use(VueRouter)

const routes = [

  {
    path:'/',
    name: 'mainPage',
    component: mainPage
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/redirect',
    name: 'test',
    component: test
  },
  {
    path: '/profile',
    name: 'profile',
    component: profile
  },
  {
    path: '/list',
    name: 'list',
    component: list
  },
  {
    path: '/chat/chat',
    name: 'chat',
    component: chat
  },
  {
    path: '/Register',
    name: 'regi',
    component: regi
  },
  {
    path: '/write',
    name: 'write',
    component: write
  },
  {
    path: '/view',
    name: 'view',
    component: view,
  },
  {
    path: '/paging',
    name: 'paging',
    component: PaginatedList
  },
  {
    path: '/mainPaging',
    name: 'mainPaging',
    component: mainPagination
  },
  {
    path: '/mainWrite',
    name: '/mainWrite',
    component: maingWrite
  },
  {
    path: '/productList',
    name: 'productList',
    component: productList
  },
  {
    path: '/mainDetail',
    name: 'mainDetail',
    component: mainDetail
  },
  {
    path: '/commentList',
    name: 'commentList',
    component: commentList
  },
  {
    path: '/image',
    name: 'image',
    component: image
  },
  {
    path: '/googleRegi',
    name: 'googleRegi',
    component: googleRegi
  }




]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
