<template>
  <div>
    <h1>게시판 등록</h1>

    <div class="AddWrap">
        <table class="tbAdd">
          <colgroup>
            <col width="15%" />
            <col width="*" />
          </colgroup>
          <tr>
            <th>제목</th>
            <td><input type="text" v-model="input.title" ref="title"/></td>
          </tr>
          <tr>
            <th>첨부파일</th>
            <td>
                <input multiple type="file" id="files" ref="serveyImage"  @change="onInputImage" accept=".jpg, .png, .jpeg">
            </td>
          </tr>
          <tr>
            <th>내용</th>
            <td><textarea v-model="input.content" ref="content" ></textarea></td>
          </tr>
        </table>
    </div>

    <div class="btnWrap">
      <button @click="goBack">목록</button>
      <button @click="setList">등록</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {

  data() {
    return{
      input: {
        id: '',
        title: '',
        content: '',
        seq:'',
        image:''
      }
    }
  },

  mounted() {

    let seq = localStorage.getItem("seq")
    this.input.seq = seq

    if(seq==null){

      alert("로그인 후 이용 가능합니다.")
      this.$router.push({
        name:'login'
      })
    }

    const seqNumber = {
      "mno": seq
    }

    axios.post("/api/member/data",seqNumber)
    .then(res =>{
      var value = res.data[0];
      this.input.id = value.id;
    })
    .catch(err =>{
      console.log(err)
    })
    },

  methods:{

    onInputImage(){
      this.input.image = this.$refs.serveyImage.files
      console.log(this.input.image)
    },

    goBack(){
      this.$router.push({
        name:'list'
      });

    },

    setList() {

      if(!this.input.title) {
        alert('제목을 입력해 주세요');
        this.$refs.title.focus();
        return;
      }
      if(!this.input.content){
        alert('내용을 입력해 주세요')
        this.$refs.content.focus();
      }

      const bookData = new FormData();

      bookData.append('id',this.input.id)
      bookData.append('title',this.input.title)
      bookData.append('content',this.input.content)

      for(let i = 0; i<this.input.image.length; i++){
        bookData.append('files',this.input.image[i])
      }



      this.$axios.post('/api/board/write',bookData,{
        headers:{
          'Content-Type': 'multipart/form-data'
        }
      })
        .then(res=>{

          console.log("response data : "+res.data);

          if(res.data==true) {
            alert('등록되었습니다.');
            this.goBack();

          } else {
            alert("실행중 실패했습니다.\n다시 이용해 주세요");
          }
        })
        .catch((err)=>{
          console.log(err);
        })

    }
  }
}
</script>

<style scoped>

.AddWrap{; display: flex; position: relative; width: 1200px; margin:0 auto; }
.tbAdd{ position:relative; width: 500px; margin:0 auto;}


.tbAdd{border-top:1px solid #888;}
.tbAdd th, .tbAdd td{border-bottom:1px solid #eee; padding:5px 0;}
.tbAdd td{padding:10px 10px; box-sizing:border-box;}
.tbAdd td input{width:100%; min-height:30px; box-sizing:border-box; padding:0 10px;}
.tbAdd td textarea{width:100%; min-height:300px; padding:10px; box-sizing:border-box;}
.btnWrap button{  border: 1px solid #ff8700; background-color: white; color: #ff8700; padding: 5px;
  border-top-left-radius:5px;
  border-bottom-left-radius:5px;
  border-top-right-radius:5px;
  border-bottom-right-radius:5px;
  border-top-left-radius:5px;}

</style>
