<template>
  <div class="main-container">
    <h2 class="header">상품 등록</h2>
    <hr>
    <div>
      &nbsp<h4>상품 이미지</h4><br>
      <input type="file" ref="serveyImage" id="files" @change="onInputImage" accept=".jpg, .png, .jpeg" multiple>
    </div>
    <hr>
    <div class="title">
      <span>제목 :</span> <input type="text" ref="title" placeholder="상품 제목을 입력해주세요" v-model="title">
    </div>
    <hr>
    <div>
      <span> 카테고리 :</span>&nbsp
      <select v-model="categori" ref="categori">
        <option disabled value="">Please select one</option>
        <option value="ee">전자기기</option>
        <option value="baby">육아용품</option>
        <option value="car">자동차용품</option>
      </select>
    </div>
    <hr>
    <div>
      <span> 상품상태 :</span>&nbsp
      <input type="radio" id="old" value="old" v-model="status">
      <label for="old">중고상품</label>
      <input type="radio" id="new" value="new" v-model="status">
      <label for="new">새상품</label>
    </div>
    <hr>
    <div>
      <span> 고환여부 :</span>&nbsp
      <input type="radio" id="impossible" value="impossible" v-model="exchange">
      <label for="impossible">교환불가</label>
      <input type="radio" id="possible" value="possible" v-model="exchange">
      <label for="possible">교환가능</label>
    </div>
    <hr>
    <div>
      <span> 물품가격 :</span>&nbsp
      <input type="text" v-model="money" placeholder="상품의 가격" ref="money"> 원
    </div>
    <hr>
    <div>
      <span> 거래위치 :</span>&nbsp
      <select v-model="place" ref="place">
        <option disabled value="">Please select one</option>
        <option value="YTN">YTN</option>
        <option value="DQ">Digital Cube</option>
        <option value="IFC">IFC</option>
      </select>
    </div>
    <hr>
    <div>
      <span> 상품설명 </span><br>
      <textarea placeholder="상품의 설명을 입력해주세요" v-model="content" ref="content"></textarea>

<!--      <div class="textarea" contenteditable="true" v-model="content" ref="content">-->
<!--        <img src="@/assets/00e3a1a7ff974c51892343cd4c5242dc.png">-->
<!--      </div>-->
    </div>
    <hr>
    <div class="applyBtn">
      <button type="button" @click="setList">상품 등록</button>
    </div>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    data(){
      return{
        id:'',
        title:'',
        content:'',
        status:'',
        exchange:'',
        money:'',
        categori:'',
        place:'',
        image:'',
        seq:'',
        imgFile:''
      }
    },

    mounted() {

      let seq = localStorage.getItem("seq")
      this.seq = seq

      if(seq==null){

        alert("로그인 후 이용 가능합니다.")

        this.$router.push({
          name:'login'
        })

      }

      const seqNumber = {
        "mno": this.seq
      }

      axios.post("/api/member/data",seqNumber)
        .then(res =>{
          var value = res.data[0];
          this.id = value.id;
        })
        .catch(err =>{
          console.log(err)
        })

    },
    methods:{
      onInputImage(){
        this.image = this.$refs.serveyImage.files
        console.log(this.image)
      },


      goBack(){
        this.$router.push({
          name:'list'
        });

      },

      setList() {

        if(!this.title) {
          alert('제목을 입력해 주세요');
          this.$refs.title.focus();
          return;
        }
        if(!this.content){
          alert('상품을 설명해 주세요')
          this.$refs.content.focus();
          return;
        }

        if(!this.status || !this.exchange || !this.categori) {
          alert('상품 설명이 부족합니다')
          this.$refs.categori.focus();
          return;
        }


        const bookData = new FormData();

        bookData.append('id',this.id)
        bookData.append('title',this.title)
        bookData.append('content',this.content)
        bookData.append('status',this.status)
        bookData.append('exchange',this.exchange)
        bookData.append('money',this.money)
        bookData.append('categori',this.categori)
        bookData.append('place',this.place)

        for(let i = 0; i<this.image.length; i++){
          bookData.append('files',this.image[i])
        }

        this.$axios.post('/api/product/write',bookData,{
          headers:{
            'Content-Type': 'multipart/form-data'
          }
        })
          .then((res)=>{

            // console.log("response data : "+res.data[0]);

            if(res.data[0] != null) {
              alert('등록되었습니다.');
              this.goBack();

            } else {
              alert("실행중 실패했습니다.\n다시 이용해 주세요");
            }
          })
          .catch((err)=>{
            console.log(err);
          })

      }
    }


  }
</script>

