<template>
  <div class="comment-container">
    <div class="writeBox">
      <input type="text" v-model="comment" placeholder="댓글을 입력하세요"><button type="button" @click="saveCmt">저장</button>
    </div>
    <div class="commentList">
      <table>
        <tr class="list" v-for="(cmt,idx) in list" :key="cmt.cno" @click="addAnswer(`${cmt.cno}`)">
          <td>{{cmt.id}}</td>
          <td>{{ cmt.comment }}</td>
          <td>{{ cmt.wdate.substring(0,10) }}</td>
          <div v-if="cmtId==cmt.id">
          <td><span @click="delCmt">x</span></td>
          </div>
        </tr>
      </table>

<!--      <div class="comment">-->
<!--        <div class="comment-content">-->
<!--          <span>test</span>-->
<!--        </div>-->
<!--        <div class="comment-info">-->
<!--          <span>test</span>-->
