<!--        </div>-->
<!--      </div>-->


    </div>
  </div>
</template>
<script>


import axios from "axios";

export default {

  name:'comment-list',

  props:['pnoNumber'],

  data(){
    return{
      num: this.pnoNumber,
      userNum:'',
      list:'',
      cmtId:'',
      comment:''
    }
  },

  mounted() {

    this.userNum = localStorage.getItem("seq");
    this.getUserData(this.userNum)
    this.getCommentList()
  },

  methods:{

    getUserData(UserSeq){
      const seq = {"mno":UserSeq}

      axios.post('/member/data',seq)
      .then(res =>{
        console.log("user id : "+res.data[0].id)
        this.cmtId = res.data[0].id

      })
      .catch(err =>{
        console.log(err)
      })
    },
    saveCmt(){

      const cmt = {
        "pno":this.num,
        "id":this.cmtId,
        "comment":this.comment
      }

      axios.post('/comment/add',cmt)
      .then(res=>{
        this.list = res.data




        // this.$router.push({
        //   name:'commentList'
        // })

      })
      .catch(err=>{
        console.log(err)
      })


    },
    getCommentList(){

      const pno = {
        "pno":this.num
      }

      axios.post('/comment/list',pno)
      .then(res=>{
        this.list = res.data
      })
      .catch(err=>{
        console.log(err)
      })
    },
    addAnswer(num){
      alert(num)
    },
    delCmt(){
      alert("test")
    }




  },
}
</script>
