<template>
  <div>
      <div class="login">
          <p>
            <input type="text" v-model.trim="memberId" placeholder="아이디를 입력하세요." />
          </p>
          <p>
            <input type="password" v-model.trim="memberPassword" placeholder="패스워드를 입력하세요." />
          </p>
          <p class="buttons">
            <button @click="doLogin" class="button blue">로그인</button>
            <button @click="doCancel" class="button">취소</button>
            <br><br>
            <input type="checkbox" id="remember" ref="remember" v-model="check" true-value="Y" false-value="N">
            <label for="remember">remember</label><br>

            <span>{{check}}</span>
          </p>

      </div>
  <br><br><br>


    <button  id="googleLoginBtn" @click="onClickGoogleLogin">구글 로그인</button>
  </div>
</template>

<script>
import axios from "axios";

export default {

    data(){
      return {
        memberId:'',
        memberPassword:'',
        check:''
      }
    },
    methods: {
      onClickGoogleLogin(googleUser){
        location.replace("https://accounts.google.com/o/oauth2/v2/auth?" +
          "client_id=648470951260-0t93ktt2sqkh6ednr8cp969snl0ucrm1.apps.googleusercontent.com&" +
          "redirect_uri=http%3A%2F%2Flocalhost%3A8081%2Fredirect&" +
          "response_type=code&" +
          "scope=profile%20openid&email" +
          "access_type=offline");
      },
      doLogin(){

        var usr = this.memberId;
        var psw = this.memberPassword;

        if(usr==""){
          alert("아이디를 입력하세요")
        }
        if(psw==""){
          alert("비밀번호를 입력하세요")
        }
        if(usr!="" && psw!=""){

          const loginUser = {
            "id":usr,
            "password":psw
          }

          axios.post('/api/login',loginUser)
          .then(res => {
            if(res.data != 0){
              // alert("로그인 완료")
              // alert(res.data)
              localStorage.setItem("seq",res.data);
              this.$router.push('/')
              this.$router.go();

            }else{
              alert('정보가 일치하지 않습니다.')
              this.memberId = "";
              this.memberPassword = "";
            }

          })
          .catch(err => {
            console.log("error: "+err)
          })
        }

      },
      doCancel(){

      },
      remember(){

      }

    },
  mounted() {
  }
}
</script>

