<template>

  <div v-if="session">

    <h2>게시판 리스트 </h2>

    <div class="listWrap">

      <main-pagination :list-array="PageArray"></main-pagination>

      <div class="btnRightWrap">
        <a @click="movePage" class="btn">등록</a>
      </div>
    </div>
  </div>

  <div v-else>
    <h1>login 후 이용 부탁드립니다.</h1>
  </div>

</template>

<script>

import axios from "axios";
import mainPagination from '@/components/page/mainPagination'

export default {

  components:{
    mainPagination
  },

  data() { //변수생성
    return{
      // board_code:'news' //게시판코드
      list:'' //리스트 데이터
      ,no:''
      ,session:false
      ,PageArray:[]
    }
  },

  mounted() {

    let seq = localStorage.getItem("seq");
    if(seq!=null){
      this.session=true
    }

    this.getList()
  },

  methods:{

    getList(){
      console.log("getList in")
      axios.post('/api/product/list')
        .then(res=>{
          console.log("success : "+res.data)
          this.PageArray = res.data
        })
        .catch(err=>{
          console.log("error : "+err)
        })
    },

    movePage(){
      this.$router.push({
        name:'write'
      });
    }
  }
}
</script>

